import { toHundreds } from "./toHundred"

const BASE_PRICE = 5000
const BASE_SIZE = 30
const BASE_PRICE_PER_SPACE =
 BASE_PRICE / (0.25 * Math.PI * BASE_SIZE * BASE_SIZE)
const PRICE_PER_STEP = 2000

const UPPER_STEPS = {
  30: 0,
  40: 1,
  50: 2,
  60: 4,
  70: 5,
  80: 6,
}

export function getCirclePrice({ diameter }) {
  const space = 0.25 * Math.PI * BASE_SIZE * BASE_SIZE
  const minPrice = toHundreds(BASE_PRICE_PER_SPACE * space)

  switch (Number(diameter)) {
    case 22:
      return 3300
    case 30:
      return minPrice + PRICE_PER_STEP * UPPER_STEPS[diameter] - 100
    case 40:
      return minPrice + PRICE_PER_STEP * UPPER_STEPS[diameter] - 100
    case 50:
      return minPrice + PRICE_PER_STEP * UPPER_STEPS[diameter] - 100
    case 60:
      return minPrice + PRICE_PER_STEP * UPPER_STEPS[diameter] - 100
    case 70:
      return minPrice + PRICE_PER_STEP * UPPER_STEPS[diameter] - 100
    case 80:
      return minPrice + PRICE_PER_STEP * UPPER_STEPS[diameter] - 100
    default:
      return `error`
  }
}