import React, { useState, useEffect } from "react"
import plural from "plural-ru"
import styles from "./Calculator.module.scss"
import { getCirclePrice, getSquarePrice, useSizes } from "./common"
import { Diameter } from "./Diameter"
import { Squares } from "./Squares"
import { Inputs } from "./Inputs"

function getPrice(isCircle, sizes) {
  switch (isCircle) {
    case true:
      return getCirclePrice(sizes)
    case false:
      return getSquarePrice(sizes)
  }

  return false
}

export default function Calculator({ header, subHeader, isCircle, ...props }) {
  const [price, setPrice] = useState(false)
  const [sizes, onChange] = useSizes(false)

  useEffect(() => {
    setPrice(getPrice(isCircle, sizes))
  }, [sizes, setPrice])

  return (
    <div className={styles.root} {...props}>
      <h1 className={styles.header}>{header}</h1>
      <h2 className={styles.subHeader}>{subHeader}</h2>

      <Inputs onChange={onChange} isCircle={isCircle} />

      <div className={styles.placeholder}>
        {sizes.diameter && <Diameter diameter={sizes.diameter} price={price} />}
        {sizes.width && sizes.height && (
         <Squares width={sizes.width} height={sizes.height} price={price} />
        )}
        {typeof price === "number" && (
         <div className={styles.price}>
           <div>
             {price} {plural(price, "рубль", "рубля", "рублей")}
           </div>
         </div>
        )}
      </div>
    </div>
  )
}
