import styles from "./Calculator.module.scss"
import React from "react"

export function Squares({ price, width, height }) {
  if (typeof price === "string") {
    return (
      <div className={styles.error}>
        Ширина и высота картины должен быть{" "}
        <b>больше или равны 30, и кратны 10</b>
      </div>
    )
  }

  return (
    <div className={styles.sizes}>
      Стоимость картины шириной <b>{width}см</b> и высотой <b>{height}см</b>
    </div>
  )
}
