import styles from "./Calculator.module.scss"
import React from "react"

export function Diameter({ diameter, price }) {
  if (typeof price === "string") {
    return (
      <div className={styles.error}>
        Диаметр картины должен быть <b>больше или равен 30, и кратен 10</b>
      </div>
    )
  }

  return (
    <div className={styles.sizes}>Стоимость картины диаметром <b>{diameter}см</b></div>
  )
}
