import { toHundreds } from "./toHundred"

const BASE_PRICE = 6000
const BASE_SIZE = 30
const BASE_PRICE_PER_SPACE =
 BASE_PRICE / (BASE_SIZE * BASE_SIZE)

export function getSquarePrice({ width, height }) {
  const space = width * height
  const minPrice = toHundreds(BASE_PRICE_PER_SPACE * space * 0.7)

  if (width >= 30 && width % 10 === 0 && height >= 30 && height % 10 === 0) {
    return minPrice
  }

  console.log('error')

  return "error"
}