import React from "react"
import styles from "./Calculator.module.scss"

export function Inputs({ isCircle, onChange }) {
  return (
    <div className={styles.inputs}>
      {isCircle ? (
        <div className={styles.input}>
          <input onChange={onChange} placeholder='диаметр' name="diameter" type="number" />
        </div>
      ) : (
        <>
          <div className={styles.input}>
            <input onChange={onChange} placeholder='ширина' name="width" type="number" />
          </div>
          <div className={styles.input}>
            <input onChange={onChange} placeholder='высота' name="height" type="number" />
          </div>
        </>
      )}
    </div>
  )
}
